/**
 * Accessibility features.
 */
/* element-invisible as defined by http://snook.ca/archives/html_and_css/hiding-content-for-accessibility */
.element-invisible,
.element-focusable {
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px); }

/* Turns off the element-invisible effect. */
.element-focusable:active, .element-focusable:focus {
  position: static !important;
  clip: auto;
  height: auto;
  width: auto;
  overflow: auto; }

@media print {
  body {
    margin: 0;
    padding: 0; }
  a {
    color: #393d40;
    text-decoration: underline; }
  .page-container {
    width: 100%;
    float: left;
    clear: both;
    display: block;
    float: none;
    margin-left: auto;
    margin-right: auto;
    padding: 0;
    direction: ltr;
    height: 100%;
    padding-bottom: 54px; }
    .page-container .page-header, .page-container .page-footer {
      display: none; }
    .page-container #sidebar-first {
      display: none; }
    .page-container #content-wrapper {
      width: 100%;
      float: left;
      margin-right: 0;
      display: block;
      font-size: 1em;
      margin-right: 0px;
      height: inherit; }
    .page-container #sidebar-second, .page-container .columns-1, .page-container .columns-2, .page-container .columns-3,
    .page-container .panel-2col .panel-col-top,
    .page-container .panel-2col .panel-col-first, .page-container .panel-2col .panel-col-last {
      clear: both;
      display: block;
      float: none;
      margin: 10px 0;
      width: 100% !important; }
  .ui-tabs-panel {
    display: block;
    border: 1px solid #BFBFBF;
    min-height: 315p  x;
    padding: 1em 1.4em;
    background: none; }
    .ui-tabs-panel .columns-1 {
      clear: both;
      display: block;
      float: none !important;
      margin: 0;
      width: 100%; }
      .ui-tabs-panel .columns-1 img {
        display: inline-block;
        margin-right: 10px;
        width: 40px; }
      .ui-tabs-panel .columns-1 br {
        display: none; }
  #appsIconsList, .appIconsDetail, #panels-ipe-tray, .ipe-tab-wrapper, .ipe-tabs, .ipe-tab {
    display: none !important;
    visibility: hidden; }
